import { API_URL } from '../utils/constants';

const axios = require('axios');

const getURL = (path: string) => {
    return `${API_URL}${path}`;
};

export const getFetch = (path: string, params: any = {}) => {
    return axios({ url: getURL(path), method: 'GET', params: params })
        .then((res: any) => res.data)
        .catch((error: any) => {
            console.log(error);
        });
};
