import { MDBCol, MDBRow } from 'mdb-react-ui-kit';
import { SingleValue } from 'react-select';
import { sortOptions } from '../../../utils/constants';
import { ISelectOption } from '../../../utils/interfaces';
import Tooltip from '../../common/tooltip';
import CustomSelect from './custom-select';

interface SortSelectProps {
    value: number;
    onChange: (value: SingleValue<ISelectOption>) => void;
}

const SortSelect = ({ value, onChange }: SortSelectProps) => {
    return (
        <>
            <MDBRow className="ms-0 text-info">Ordenação</MDBRow>
            <MDBRow>
                <MDBCol>
                    <CustomSelect
                        placeholder="Ordenação"
                        options={sortOptions}
                        value={sortOptions[value]}
                        zIndex={10}
                        onChange={(value) =>
                            onChange(value as SingleValue<ISelectOption>)
                        }
                    />
                </MDBCol>
                <MDBCol md="auto" center>
                    <Tooltip tip="Ordenação dos filmes" />
                </MDBCol>
            </MDBRow>
        </>
    );
};

export default SortSelect;
