import { TipoBusca } from '../../../utils/constants';

interface SearchTitleProps {
    tipoBusca: TipoBusca;
    totalResults: number;
}

const SearchTitle = ({ tipoBusca, totalResults }: SearchTitleProps) => {
    return (
        <>
            {tipoBusca && (
                <h1
                    className="centralizar text-info"
                    style={{ zIndex: 20, height: '50px', width: '100%' }}
                >
                    {tipoBusca}
                </h1>
            )}
            {tipoBusca === TipoBusca.Busca && totalResults > 0 && (
                <span className="centralizar text-white">
                    {totalResults} resultados
                </span>
            )}
        </>
    );
};

export default SearchTitle;
