import { MDBCol, MDBRange, MDBRow } from 'mdb-react-ui-kit';
import Tooltip from '../../common/tooltip';

interface VoteCountRangeProps {
    value: number;
    onChange: (value: number) => void;
}

const VoteCountRange = ({ value, onChange }: VoteCountRangeProps) => {
    return (
        <>
            <MDBRow>
                <MDBCol>
                    <MDBRange
                        color="white"
                        className="mb-2 text-white"
                        defaultValue={100}
                        min="0"
                        max="1000"
                        step="50"
                        label="Avaliações"
                        labelClass="rangeLabel text-info ms-0"
                        value={value}
                        onChange={(e) => onChange(parseInt(e.target.value))}
                    />
                </MDBCol>
                <MDBCol md="auto" center>
                    <Tooltip tip="Quantidade mínima de avaliações de usuário" />
                </MDBCol>
            </MDBRow>
        </>
    );
};

export default VoteCountRange;
