import { MDBCol, MDBRow } from 'mdb-react-ui-kit';
import { MultiValue, PropsValue } from 'react-select';
import { ISelectItems, ISelectOption } from '../../../utils/interfaces';
import Tooltip from '../../common/tooltip';
import CustomSelect from './custom-select';

interface GenresSelectProps {
    value: PropsValue<ISelectOption>;
    genres: ISelectItems;
    onChange: (values: MultiValue<ISelectOption>) => void;
}

const GenresSelect = ({ value, genres, onChange }: GenresSelectProps) => {
    const getGeneroOptions = () => {
        var generosOptions: ISelectOption[] = [];

        genres.map((value) =>
            generosOptions.push({ value: value.id, label: value.name })
        );

        return generosOptions;
    };

    return (
        <>
            <MDBRow className="ms-0 text-info">Gênero</MDBRow>
            <MDBRow>
                <MDBCol>
                    <CustomSelect
                        placeholder="Todos os gêneros"
                        options={getGeneroOptions()}
                        value={value}
                        zIndex={9}
                        isMulti
                        onChange={(values) =>
                            onChange(values as MultiValue<ISelectOption>)
                        }
                    />
                </MDBCol>
                <MDBCol md="auto" center>
                    <Tooltip tip="Serão retornados apenas os filmes que possuem todos os gêneros selecionados" />
                </MDBCol>
            </MDBRow>
        </>
    );
};

export default GenresSelect;
