import { MDBBtn, MDBCol, MDBIcon, MDBRow } from 'mdb-react-ui-kit';
import { useState } from 'react';
import DatePicker from 'react-datepicker';
import { formatDate } from '../../../utils/helpers';

interface ReleaseDatePickerProps {
    startDate: Date | null;
    endDate: Date | null;
    setStartDate: React.Dispatch<React.SetStateAction<Date | null>>;
    setEndDate: React.Dispatch<React.SetStateAction<Date | null>>;
}

const ReleaseDatePicker = ({
    startDate,
    endDate,
    setStartDate,
    setEndDate,
}: ReleaseDatePickerProps) => {
    const [isStartDateOpen, setIsStartDateOpen] = useState(false);
    const [isEndFinalOpen, setIsEndDateOpen] = useState(false);

    const handleStartDateChange = (e: Date) => {
        setIsStartDateOpen(!isStartDateOpen);
        setStartDate(e);
    };

    const handleEndDateChange = (e: Date) => {
        setIsEndDateOpen(!isEndFinalOpen);
        setEndDate(e);
    };

    const handleStarDateClick = (e: any) => {
        e.preventDefault();
        setIsStartDateOpen(!isStartDateOpen);
    };

    const handleEndDateClick = (e: any) => {
        e.preventDefault();
        setIsEndDateOpen(!isEndFinalOpen);
    };

    const dateStr = (placeHolder: string, data: Date | null): string => {
        if (data) {
            return `${placeHolder}: ${formatDate(data)}`;
        }

        return placeHolder;
    };

    return (
        <>
            <MDBRow className="ms-0 text-info">Data de lançamento</MDBRow>
            <MDBRow>
                <MDBCol>
                    <span id="data">
                        <MDBBtn
                            noRipple
                            type="submit"
                            color="info"
                            onClick={handleStarDateClick}
                            className="mt-1 me-2 active"
                        >
                            {dateStr('Data inicial', startDate)}
                        </MDBBtn>
                        <MDBBtn
                            noRipple
                            href="#"
                            color="none"
                            floating
                            onClick={() => setStartDate(null)}
                        >
                            <MDBIcon far color="white" icon="trash-alt" />
                        </MDBBtn>
                        {isStartDateOpen && (
                            <DatePicker
                                selected={startDate}
                                peekNextMonth
                                showMonthDropdown
                                showYearDropdown
                                dropdownMode="select"
                                locale="pt"
                                dateFormat="dd/MM/yyyy"
                                startOpen
                                withPortal
                                maxDate={new Date()}
                                onChange={handleStartDateChange}
                                onCalendarClose={() => {
                                    setIsStartDateOpen(false);
                                }}
                                customInput={<span />}
                            />
                        )}
                    </span>
                    <span id="data">
                        <MDBBtn
                            noRipple
                            type="submit"
                            color="info"
                            onClick={handleEndDateClick}
                            className="mt-1 me-2 active"
                        >
                            {dateStr('Data final', endDate)}
                        </MDBBtn>
                        <MDBBtn
                            noRipple
                            href="#"
                            color="none"
                            floating
                            onClick={() => setEndDate(null)}
                        >
                            <MDBIcon far color="white" icon="trash-alt" />
                        </MDBBtn>
                        {isEndFinalOpen && (
                            <DatePicker
                                selected={endDate}
                                peekNextMonth
                                showMonthDropdown
                                showYearDropdown
                                dropdownMode="select"
                                locale="pt"
                                dateFormat="dd/MM/yyyy"
                                startOpen
                                withPortal
                                maxDate={new Date()}
                                onChange={handleEndDateChange}
                                onCalendarClose={() => {
                                    setIsEndDateOpen(false);
                                }}
                                customInput={<span />}
                            />
                        )}
                    </span>
                </MDBCol>
            </MDBRow>
        </>
    );
};

export default ReleaseDatePicker;
