import { MDBCol, MDBRow } from 'mdb-react-ui-kit';
import { MultiValue, PropsValue } from 'react-select';
import { ISelectItems, ISelectOption } from '../../../utils/interfaces';
import Tooltip from '../../common/tooltip';
import CustomAsyncSelect from './custom-async-select';

interface CastSelectProps {
    value: PropsValue<ISelectOption>;
    loadOptions: (keyword: string) => Promise<any[]>;
    onChange: (values: MultiValue<ISelectOption>) => void;
}

const CastSelect = ({ value, loadOptions, onChange }: CastSelectProps) => {
    return (
        <>
            <MDBRow className="ms-0 text-info">Atores</MDBRow>
            <MDBRow>
                <MDBCol>
                    <CustomAsyncSelect
                        placeholder="Digite um nome..."
                        loadOptions={loadOptions}
                        value={value}
                        zIndex={8}
                        isMulti={true}
                        onChange={(values) =>
                            onChange(values as MultiValue<ISelectOption>)
                        }
                    />
                </MDBCol>
                <MDBCol md="auto" center>
                    <Tooltip tip="Serão retornados apenas os filmes em que todas as pessoas selecionadas participaram" />
                </MDBCol>
            </MDBRow>
        </>
    );
};

export default CastSelect;
